<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <base-material-card
        class="px-5 py-3 "
        color="indigo"
        icon="mdi-desktop-mac"
        inline
      >
        <template v-slot:after-heading>
          <div class="display-2 font-weight-light">
            {{ $t('BonTemplate') }}
          </div>
        </template>
        <v-row>
          <v-col
            cols="12"
            md="6"
            sm="6"
          >
            <v-select
              v-model="ResItem.class"
              :items="ClassList"
              item-text="class"
              item-value="class"
              :label="$t('Choose your section')"
              @change="reloadItem (ResItem.class)"
            />
          </v-col>
        </v-row>
        <v-divider class="mt-3"/>
        <v-data-table
          :fixed-header="true"
          :headers="headers"
          :items="desserts"
          :items-per-page="15"
          :loading="loading"
          multi-sort
        >
          <template v-slot:item.action="{ item }">
            <v-icon
              class="mr-2"
              small
              @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn
              color="primary"
              @click="initialize"
            >
              Reset
            </v-btn>
          </template>
          <template v-slot:footer>
            <v-toolbar
              color="white"
              flat
            >
              <v-spacer/>
              <v-btn
                class="mb-2"
                color="primary"
                dark
                @click="insertBon"
              >
                {{ $t('增加默认厨房单') }}
              </v-btn>
            </v-toolbar>
          </template>
        </v-data-table>
      </base-material-card>
      <v-dialog
        v-model="dialog"
        max-width="50%"
      >
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  md="6"
                  sm="6"
                >
                  <v-text-field
                    label="ID"
                    value="Auto"
                    disabled
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                  sm="6"
                >
                  <v-text-field
                    v-model="editedItem.sectionSort"
                    :label="$t('sectionSort')"
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="12"
                  sm="12"
                >
                  <v-textarea
                    v-model="editedItem.content"
                    :label="$t('content')"
                    auto-grow
                    outlined
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn
              color="blue darken-1"
              text
              @click="close"
            >
              Cancel
            </v-btn>
            <v-btn
              color="blue darken-1"
              @click="save"
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </v-container>
</template>

<script>

  import { getBonTemplateList, getBonTemplateClassList, updateBonTemplate, insertBonTemplate } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    data: function () {
      return ({
        loading: true,
        dialog: false,
        Utils: Utils,
        headers: [
          {
            text: 'Id',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          {
            text: this.$i18n.t('class'),
            value: 'class',
          },
          {
            text: this.$i18n.t('sectionName'),
            value: 'section',
          },
          {
            text: this.$i18n.t('sectionSort'),
            value: 'sectionSort',
          },
          {
            text: this.$i18n.t('content'),
            value: 'content',
          },
          {
            text: this.$i18n.t('action'),
            value: 'action',
            sortable: false,
          },
        ],
        desserts: [],
        ClassList: [],
        editedIndex: -1,
        ResItem: {
          class: '',
        },
        editedItem: {
          sectionSort: '',
          content: '',
          id: -1,
        },
      })
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? this.$i18n.t('Edit') : this.$i18n.t('Edit')
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      insertBon () {
        insertBonTemplate(this.ResItem.class).then((res) => {
          Utils.toast('Successful!')
          this.close()
          this.initialize()
        })
      },
      async initialize () {
        await getBonTemplateList().then(res => {
          this.ClassList = res.content
          this.ResItem.class = this.ClassList[0].class
          this.reloadItem(this.ResItem.class)
        })
      },
      reloadItem (name) {
        getBonTemplateClassList(name).then(res => {
          this.loading = false
          this.desserts = res.content
        })
      },
      editItem (item) {
        console.log(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
        this.editedItem.sectionSort = '5'
      },
      save () {
        updateBonTemplate(this.editedItem.id, this.editedItem.sectionSort, this.editedItem.content).then((res) => {
          Utils.toast('Successful!')
          this.close()
          this.initialize()
        })
      },
      close () {
        this.dialog = false
      },
    },
  }
</script>
